import cookies from "vue-cookies";
import imageUpload from "../public/imageUpload.vue";
import ImagePreview from "../public/imagePreview.vue";
export default {
  components: { imageUpload, ImagePreview },
  data() {
    return {
      username: "",
      password: "",
      email: "",
      birthday: "",
      location: "",
      bio: "",
      icon: "",
      type_user: "user",
    };
  },
  props: {
    image_type: {
      type: String,
    },
  },
  methods: {
    async signUp() {
      await this.$axios({
        url: "/api/users",
        method: "post",
        data: {
          username: this.username,
          password: this.password,
          email: this.email,
          birthday: this.birthday,
          location: this.location,
          bio: this.bio,
          icon: this.icon,
        },
      })
        .then((response) => {
          console.log(response.data);
          this.user = response.data;
          cookies.set("user", response.data);
          cookies.set("token", response.data.token);
          this.$router.push("/");
        })
        .catch((error) => {
          console.log(error);
        });
    },
    back() {
      this.$emit("pop_up_false", false);
    },

    newImage(data) {
      this.icon = data;
      console.log(this.icon);
    },
    delete_res(data) {
			console.log(data)
			switch (data) {
				case true:
						this.icon = null
					break;
				case false:
						alert('Delete falied')
					break;
			
				default:
					break;
			}
		}
  },
};
